@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
.sidebar {
    position: fixed;
    width: 12rem;
    background-color: white;
    padding-left: 60px;
    height: 100vh;
}

.sidebar .linie {
    width: calc(100% - 60px);
    height: 1px;
    display: block;
    background-color: #c9c9c9;
}

.sidebarElement,
.sidebar h3 {
    padding: 1rem 0;
}

.sidebar h3:nth-child(1) {
    padding-top: 2rem;
}

.sidebarElement p {
    position: absolute;
    left: 6.5rem;
}

.sidebarElement {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.sidebarElement:hover .icon {
    color: #256fdf;
}

.sidebarElement:hover p {
    color: #14275a;
}

.sidebar .logo {
    padding-top: 70px;
    padding-bottom: 2rem;
    margin-bottom: 0px;
    width: 10rem;
}

.sidebarElement p,
.sidebarElement .icon {
    color: #9ba6c4;
}

.sidebarElement .icon {
    color: #9ba6c4;
    font-size: 1.2rem;
}

.sidebar .logoContainer {
    width: calc(100% - 60px);
    display: flex;
    justify-content: center;
}

.sidebar .selected .icon {
    color: #256fdf;
}

.sidebar .selected p {
    color: #14275a;
}

.sidebar .account {
    pointer-events: none;
}

.menueIcon {
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #256fdf;
    text-align: center;
    color: white;
    padding: 1rem;
    cursor: pointer;
    display: none;
}

.menueIcon h4 {
    color: white;
}

.menueIcon h4:hover{
    font-style: italic;
}

.back {
    display: none;
}

* {
    margin: 0;
    padding: 0;
    font-family: "Montserrat", sans-serif;
}

body {
    background-color: #edf0f6;
}

.userLogo {
    background-color: #9ba6c4;
    height: 1.2rem;
    width: 1.2rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.user {
    text-decoration: none;
}

.userLogo p {
    position: static;
    color: white;
    font-size: 0.9rem;
}

p,
button {
    font-weight: 600;
    font-size: 1.1rem;
    color: #14275a;
}

h1 {
    font-weight: 600;
    color: #14275a;
    font-size: 4.5rem;
}

h2 {
    font-size: 2.5rem;
    font-weight: 600;
    color: #14275a;
}

h3 {
    font-size: 1.6rem;
    font-weight: 600;
    color: #14275a;
}

h4 {
    font-size: 1.3rem;
    font-weight: 600;
    color: #14275a;
}

.linie {
    height: 1px;
    display: block;
    background-color: #c9c9c9;
}

.app {
    display: flex;
}

.main {
    margin-left: calc(12rem + 60px);
    flex-grow: 1;
    display: flex;
    justify-content: center;
    padding: 3.125rem 0;
}
.main .content {
    width: 100%;
    max-width: 700px;
    border-right: 1px #c9c9c9 solid;
    border-left: 1px #c9c9c9 solid;

    padding: 1.56rem 3.125rem;
}

@media only screen and (max-width: 1050px){
    .open {
        display: block;
        z-index: 5;
    }
    .open .back {
        color: red;
        position: absolute;
        right: 0rem;
        background-color: white;
        padding: 1rem;
        display: block;
        padding-top: 0;
        cursor: pointer;
    }
    .close {
        display: none;
    }
    .main {
        width: 100%;
        margin: 0;
        padding-top: 5rem;
    }
    .main .content {
        width: 90%;
        border: none;
        padding: 0;
    }

}

.post {
    display: flex;
    background-color: transparent;
    border-radius: 1.3rem;
    border: white 0.4rem solid;
}

.post .mark {
    height: 10rem;
    width: 10rem;
    background-color: #b2edff;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0.3125rem;
    border-top-right-radius: 1.25rem;
    border-bottom-right-radius: 1.25rem;
    z-index: -1;
}

.post .informations {
    overflow: hidden;
    background-color: white;
    flex-grow: 1;
    margin-left: -1.25rem;
    z-index: -2;
    padding: 0.625rem;
    padding-left: 2.5rem;
    position: relative;
}

.post p,
.post h4 {
    color: #9ba6c4;
}

.post h4 {
    margin-top: 0.3125rem;
    max-width: 30.625rem;
    overflow: hidden;
}

.post .date {
    position: absolute;
    bottom: 0.625rem;
    left: 2.5rem;
}

.head {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    border-bottom: 1px #c9c9c9 solid;
    padding-bottom: 1rem;
}

@media only screen and (max-width: 600px) {
    .post {
        flex-direction: column;
    }
    .post .mark {
        width: 100%;
        border-top-right-radius: 0;
        border-bottom-left-radius: 1.25rem;
    }
    .post .informations {
        margin-left: 0;
        padding: 1.25rem;
        text-align: center;
        margin-top: -1.25rem;
        padding-top: 2.5rem;
    }
    .post .date {
        position:relative;
        left: 0;
        bottom: 0;
        margin-top: 0.5rem;
    }
}

input,
textarea {
    font-weight: 600;
    font-size: 1.1rem;
    color: #14275a;
    border: solid #9ba6c4 1px;
    border-radius: 0.3125rem;
    padding: 0.4375rem;
    margin: 0.0625rem;
    height: 1.25rem;
}

input::-webkit-input-placeholder {
    color: #9ba6c4;
}

input:-ms-input-placeholder {
    color: #9ba6c4;
}

input::placeholder {
    color: #9ba6c4;
}

input:focus,
textarea:focus {
    border: none;
    outline: none;
    border: solid #256fdf 2px;
    margin: 0;
}

.middle {
    text-align: center;
}

button {
    background-color: #256fdf;
    color: white;
    outline: none;
    border: none;
    padding: 0.4375rem;
    border-radius: 1.25rem;
    width: 9.375rem;
    cursor: pointer;
}

.home .filter {
    display: flex;
    grid-gap: 0.625rem;
    gap: 0.625rem;
    padding-bottom: 0.3125rem;
    cursor: pointer;
}

.home .filter p:hover {
    color: #256fdf;
}

.home .filter .selected {
    color: #256fdf;
}

.posts {
    display: flex;
    flex-direction: column;
    grid-gap: 1.25rem;
    gap: 1.25rem;
}

.posts > .postComplete {
    margin: 0.46875rem 0;
}

.posts > .postComplete:first-child {
    margin-top: 1.5rem;
}

.postComplete .userLogo {
    height: 1.4rem;
    width: 1.4rem;
}

.postComplete {
    cursor: pointer;
}

.postComplete .icon {
    font-size: 1.4rem;
}

.postComplete .postInformations {
    display: flex;
    justify-content: space-between;
}

.postComplete .postInformations .user,
.postComplete .postInformations .like {
    display: flex;
    grid-gap: 0.625rem;
    gap: 0.625rem;
    align-items: center;
}

.postComplete .postInformations p:nth-child(2) {
    color: #9ba6c4;
}

.postComplete .postInformations {
    margin: 0.625rem 0.3125rem;
    margin-bottom: 0;
}

@media only screen and (max-width: 600px) {
    .head {
        flex-direction: column;
    }
    .home .filter {
        display: flex;
        padding-bottom: 0.3125rem;
        cursor: pointer;
        justify-content: space-between;
        width: 100%;
        padding-top: 0.25rem;
    }
}

.content .create .section {
    display: flex;
    justify-content: space-between;
    margin-top: 2rem;
    grid-gap: 2rem;
    gap: 2rem;
}

.content .create .section p {
    margin-bottom: 1rem;
}

.content .create .section .description {
    height: 6.25rem;
    width: calc(100% - 1rem);
}

textarea {
    resize: none;
}

.content .create .section .divS2 {
    flex-grow: 1;
    width: 100%;
    position: relative;
}

.content .create .section .colors {
    display: flex;
    justify-content: space-between;
    cursor: pointer;
}

.content .create .section .colors .color {
    width: 2.25rem;
    height: 2.25rem;
    border-radius: 50%;
}

.content .create .section .colors .selected {
    border: #707070 3px solid;
    width: 1.875rem;
    height: 1.875rem;
}


.content .preview {
    margin-top: 2rem;
    border-top: 1px #c9c9c9 solid;
}

.content .preview #title {
    margin: 2rem 0;
}


@media only screen and (min-width: 600px) {
.content .create .section .divS2 button {
    position: absolute;
    right: 0;
    bottom: 0.25rem;
}
}
@media only screen and (max-width: 600px) {
    .create .section {
        flex-wrap: wrap;
    }
    .content .create button {
        position: relative;
        margin: 0 auto;
        margin-top: 2rem;
        width: 100%;
    }

    .create .section div{ 
        width: 100%;
    }
}

.main .content .profile .userLogo {
    width: 160px;
    height: 160px;
}

.main .content .profile .userLogo p {
    font-size: 6rem;
}

.main .content .profile {
    display: flex;
    align-items: center;
    margin: 1.5rem 0;
}

.main .content .profile .informations {
    flex-grow: 1;
}

.main .content .profile .informations .title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1.25rem;
}
.main .unfollow {
    color: #256fdf;
    background-color: white;
    border: 2px solid #256fdf;
}

.main .content .profile .data {
    display: flex;
}

.main .content .profile .data .dataElement {
    display: flex;
    align-items: baseline;
    grid-gap: 0.625rem;
    gap: 0.625rem;
}

.main .content .profile .data .dataElement p {
    color: #9ba6c4;
}

.main .content .posts {
    border-top: 1px #c9c9c9 solid;
    padding-top: 1.5rem;
}

.profile input {
    background: url(/NotenFlex_Client/static/media/search.b94db18f.svg) no-repeat scroll 4px 4px;
    background-size: 8%;
    background-position-y: center;
    background-position-x: 7px;
    padding-left: 2rem;
    width: 150px;
}

.main .content .search {
    margin-top: 1.5rem;
}

.main .content .mainInfos {
    display: flex;
    flex-direction: column;
    grid-gap: 1.25rem;
    gap: 1.25rem;
}
@media only screen and (min-width: 600px) {
    .main .content .profile .data {
        justify-content: space-between;
    }
    .main .content .profile {
        grid-gap: 50px;
        gap: 50px;
    }
}
@media only screen and (max-width: 600px) {
    .profile input {
        width: 100%;
        background-size: 6%;
        margin-top: 0.5rem;
    }
    .mainInfos .profile,
    .search .profile {
        flex-direction: column;
        grid-gap: 1.25rem;
        gap: 1.25rem;
    }
    .profile .informations .title h2,
    .search .profile .h2,
    .search .profile .title a {
        width: 100%;
        text-align: center;
    }
    .profile .informations .data {
        grid-gap: 1rem;
        gap: 1rem;
        flex-wrap: wrap;
        justify-content: center;
    }

    .profile .informations .title {
        grid-gap: 1.25rem;
        gap: 1.25rem;
        flex-direction: column;
    }
    .main .content .profile {
        margin-bottom: 3rem;
    }
}

.fullLogin {
    width: 100%;
    z-index: 2;
    display: flex;
}

.login {
    z-index: 11;
    background-color: white;
    height: 100vh;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
}

.login h1 {
    color: #256fdf;
}

.login .linie {
    width: 90%;
    max-width: 20rem;
}

.login .button {
    width: 90%;
    max-width: 20rem;
}

.login button {
    width: 100%;
}
.login input{
    width: calc(100% - 0.875rem)
} 


.login .input {
    width: 90%;
    max-width: 20rem;

    display: flex;
    flex-direction: column;
    grid-gap: 0.625rem;
    gap: 0.625rem;
}

.login .button p {
    text-align: center;
}

.login .grey button {
    background-color: #9ba6c4;
}

.presentation {
    width: 50%;
    background-color: #edf0f6;
    position: relative;
    overflow: hidden;
    height: 100vh;
}

.presentation .circle {
    width: 500px;
    height: 500px;
    background-color: #256fdf;
    opacity: 10%;
    border-radius: 50%;
    position: absolute;
    right: -200px;
    bottom: -250px;
}

.presentation .square {
    width: 11.25rem;
    height: 11.25rem;
    background-color: #256fdf;
    opacity: 10%;
    position: absolute;
    transform: rotate(-106deg);
    right: 100px;
    top: 200px;
}

.presentation .triangle {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 70.3125rem 12rem 70.3125rem;
    border-color: transparent transparent rgba(37, 111, 223, 0.1) transparent;
    position: absolute;
    transform: rotate(27deg);
    bottom: 200px;
    left: 100px;
}

.presentation img {
    width: 250px;
    margin-bottom: 90px;
}

.presentation .infos {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 90px;
}

.presentation h3 {
    text-align: left;
    color: #256fdf;
    margin-left: 50px;
}

.description {
    margin-right: 200px;
    display: flex;
    flex-direction: column;
    grid-gap: 2rem;
    gap: 2rem;
}

.presentation .linie {
    width: 100%;
}

@media only screen and (max-width: 1300px) {
    .login {
        width: 100%;
    }

    .fullLogin {
        flex-direction: column;
    }

    .presentation {
        width: 100%;
    }
}

.log {
    width: 100%;
}

